"use client";

import { useEffect, useState } from "react";
import api from "@/utils/api";
import { Minus, Plus, Trash2 } from "lucide-react";
import Link from "next/link";

export default function CheckoutPage() {
  const [cartItems, setCartItems] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  // Fetch cart from backend (user-specific)
  const fetchCart = async () => {
    try {
      const res = await api.get("/cart"); // must return items for logged-in user only
      setCartItems(res.data);
    } catch (err) {
      console.error("Error fetching cart:", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCart();
  }, []);

  // Update quantity in backend
  const updateQty = async (id: string, quantity: number) => {
    if (quantity < 1) return;

    const res = await api.put(`/cart/${id}`, { quantity });
    fetchCart(); // refresh UI from backend
  };

  const removeItem = async (id: string) => {
    await api.delete(`/cart/${id}`);
    fetchCart();
  };

  const clearCart = async () => {
    await api.delete(`/cart`);
    fetchCart();
  };

  if (loading) return <p className="p-10">Loading...</p>;

  const isCartEmpty = cartItems.length === 0;

  const total = cartItems.reduce(
    (acc, item) => acc + item.price * item.quantity,
    0
  );

  return (
    <div className="max-w-5xl mx-auto p-6">
      <h1 className="text-4xl font-bold text-black mb-8">
        Checkout <span className="text-orange-600">Summary</span>
      </h1>

      {/* EMPTY CART */}
      {isCartEmpty && (
        <div className="text-center py-20">
          <h2 className="text-2xl font-semibold text-gray-700">
            Your cart is empty ðŸ›’
          </h2>

          <Link
            href="/restaurant"
            className="mt-6 inline-block bg-orange-600 text-white px-6 py-3 rounded-lg font-semibold hover:bg-orange-700 transition"
          >
            Explore Restaurants
          </Link>
        </div>
      )}

      {/* CHECKOUT PAGE */}
      {!isCartEmpty && (
        <div className="grid md:grid-cols-3 gap-10">
          {/* LEFT SIDE */}
          <div className="md:col-span-2 space-y-5">
            {cartItems.map((item) => (
              <div
                key={item.id}
                className="flex items-center gap-5 bg-white p-4 shadow-md rounded-xl border"
              >
                <img
                  src={item.menuItem.image}
                  className="h-24 w-24 rounded-xl object-cover"
                />

                <div className="flex-1">
                  <h2 className="text-xl font-semibold text-black">
                    {item.menuItem.name}
                  </h2>

                  <p className="text-gray-600 text-sm mt-1">
                    â‚¹ {item.price} Ã— {item.quantity}
                  </p>

                  <div className="flex items-center gap-3 mt-3">
                    <button
                      onClick={() => updateQty(item.id, item.quantity - 1)}
                      className="p-2 rounded-full bg-gray-200 hover:bg-gray-300"
                    >
                      <Minus className="w-4 h-4 text-black" />
                    </button>

                    <span className="text-lg font-semibold text-black">
                      {item.quantity}
                    </span>

                    <button
                      onClick={() => updateQty(item.id, item.quantity + 1)}
                      className="p-2 rounded-full bg-gray-200 hover:bg-gray-300"
                    >
                      <Plus className="w-4 h-4 text-black" />
                    </button>
                  </div>
                </div>

                <button
                  onClick={() => removeItem(item.id)}
                  className="text-red-500 hover:text-red-700"
                >
                  <Trash2 className="w-6 h-6" />
                </button>
              </div>
            ))}
          </div>

          {/* SUMMARY BOX */}
          <div className="bg-white shadow-lg p-6 rounded-xl border">
            <h2 className="text-2xl font-bold text-black mb-4">
              Order Summary
            </h2>

            <div className="flex justify-between text-lg font-medium text-black mb-3">
              <span>Subtotal</span>
              <span>â‚¹ {total}</span>
            </div>

            <div className="flex justify-between text-lg text-gray-700 mb-3">
              <span>Delivery Fee</span>
              <span>â‚¹ 40</span>
            </div>

            <hr className="my-4" />

            <div className="flex justify-between text-2xl font-bold text-black mb-6">
              <span>Total</span>
              <span>â‚¹ {total + 40}</span>
            </div>

            <button className="w-full bg-orange-600 text-white py-3 rounded-xl font-semibold hover:bg-orange-700 transition">
              Place Order
            </button>

            <button
              onClick={clearCart}
              className="w-full mt-3 text-red-500 font-medium hover:underline"
            >
              Clear Cart
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
